
import axios from "axios";
import { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router-dom";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

export function Singup() {
  let dispatch = useDispatch();
  let params = useParams();

  let [users, setUser] = useState(null);
  

  // let data = useSelector(function (store) {
  //   return store.userSection;
  // });
     

  useEffect(function(){

    axios.get('/user_lao?id='+params.id).then(function(resp){
      setUser(resp.data)
    })
    // let userMilgya = data.users.find((user) => user.name == params.name);
    // if (userMilgya) {
    //   setUser(userMilgya);
    // }
  },[]);

  useEffect(
    function () {
      reset(users);
    }, [users]);

  function userSaver(meraData) {
    if (params.id) {
      // dispatch({
      //   type: "USER_EDIT",
      //   payload: meraData,
      // });
    
    axios.put('/user_update',meraData).then(function(resp){
         
    })



    }else {
      meraData.id = Math.round(Math.random() * 1000);
      // dispatch({
      //     type: "USER_ADD",
      //     payload: meraData,
      //   });

      let meraForm = new FormData();
      meraForm.append('id',meraData.id)
      meraForm.append('name',meraData.name)
      meraForm.append('email',meraData.email)
      meraForm.append('password',meraData.password)
      meraForm.append('pic',meraData.file[0])
        axios.post('/user_add',meraForm).then(function(){
          toast.success('Singup successfull!')
        }).catch(function(resp){
            console.log(resp);
        })
    }


  }

  let {register,handleSubmit,formState: { errors },reset} = useForm();
  return (
    <div>
      <form onSubmit={handleSubmit(userSaver)}>
        <input type="text" {...register("name", { required: true })} />
        {errors.name ? (
          <div className="error">Please enter your name</div>
        ) : null}

        <input
          type="text"
          {...register("email", {
            required: true,
            validate: function (userType) {
              if (
                userType.match(
                  /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
                )
              ) {
                return true;
              } else {
                return false;
              }
            },
          })}
        />
        {errors.email ? (
          <div className="error">Please enter your email</div>
        ) : null}

        <input
          type="password"
          {...register("password", { required: true, minLength: 8 })}
        />
        {errors.password ? (
          <div className="error">Please enter your password</div>
        ) : null}

        <input type='file' {...register('file')}/>
        <button>Submit</button>
      </form>
    </div>
  );
}
