import { combineReducers, createStore } from "redux";

let userInitialData = {
  //  users:[],
   currentUser:{}
} 
function userSection(oldData=userInitialData, newData) {
  if(newData.type=="USER_ADD"){
    oldData.users.push(newData.payload)
  }else if(newData.type=="LOG_IN"){
    oldData.currentUser = newData.payload
  }else if(newData.type=='LOG_OUT'){
      oldData.currentUser = {}
      localStorage.removeItem('meraToken')
  }else if(newData.type=="USER_DELETE"){
    oldData.users.splice(newData.index,1)
  }else if(newData.type=="USER_EDIT"){
    let someIndex = oldData.users.findIndex(user=> user.id === newData.payload.id);
    oldData.users[someIndex] = newData.payload
  }
  return {...oldData}
}

let productInitialData = {
  products:[]
}
function ProductSection(oldData=productInitialData, newData) {
  if(newData.type=="PRODUCT_ADD"){
     oldData.products.push(newData.payload)
  }
  return {...oldData}
}

let data = combineReducers({userSection,ProductSection})

export let meraStore = createStore(data);























