import axios from "axios"
import { useState } from "react"
import { useEffect } from "react"
import { useSelector } from "react-redux"
import './dashboard.css'
export  function Dashboard() {
    let userId= useSelector(function(store){
    return store.userSection.currentUser.id
    //    return store.ProductSection.products.filter(product=>product.userId == userID)
    })


useEffect(function(){
    axios.get('/personal_cards?anc='+userId).then(function(resp){
        console.log('check-kitta',resp.data);
          setProducts(resp.data)
    })
},[])



    let [products,setProducts] = useState([])
    return <div>
        <table border={1}> 
            {
                products.map(function(product){
                    return <tr>
                        <td>{<img className="imgwidth" src={product.pic} />}</td>
                        <td>{product.title}</td>
                        <td>{product.price}</td>
                    </tr>
                })
            }
        </table>
    </div>
}
