import axios from "axios";
import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

export function Login(props) {
  let { register, handleSubmit } = useForm();
  let dispatch = useDispatch();
  let someNavigate = useNavigate();
  let users = useSelector(function (store) {
    return store.userSection.users;
  });

  return (
    <div>
      <main>
        <center>
          <img
            className="responsive-img"
            style={{ width: "250px" }}
            src="https://i.imgur.com/ax0NCsK.gif"
          />
          <div className="section" />
          <h5 className="indigo-text">Please, login into your account</h5>
          <div className="section" />
          <div className="container">
            <div
              className="z-depth-1 grey lighten-4 row"
              style={{
                display: "inline-block",
                padding: "32px 48px 0px 48px",
                border: "1px solid #EEE",
              }}
            >
              <form
                className="col s12"
                method="post"
                onSubmit={handleSubmit(async function (logData) {

                  let resp_orgnl = await axios.post("/login_data", logData);
                  let resp = resp_orgnl.data
  
                  if (resp) {
                    localStorage.setItem('meraToken',resp.meraToken)
                    dispatch({
                      type: "LOG_IN",
                      payload: resp.userMillGya
                    });
                    someNavigate("/dashboard");
                    toast.success("Login Done");
                  } else {
                    toast.error("Enter correct email & password !");
                  }
                })}
              >
                <div className="row">
                  <div className="col s12"></div>
                </div>
                <div className="row">
                  <div className="input-field col s12">
                    <input
                      className="validate"
                      type="text"
                      name="email"
                      id="email"
                      {...register("email", {
                        required: true,
                        validate: function (userTypes) {
                          if (
                            userTypes.match(
                              /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
                            )
                          ) {
                            return true;
                          } else {
                            return false;
                          }
                        },
                      })}
                    />
                    <label htmlFor="email">Enter your email</label>
                  </div>
                </div>
                <div className="row">
                  <div className="input-field col s12">
                    <input
                      className="validate"
                      type="password"
                      name="password"
                      id="password"
                      {...register("password")}
                    />
                    <label htmlFor="password">Enter your password</label>
                  </div>
                  <label style={{ float: "right" }}>
                    <a className="pink-text" href="#!">
                      <b>Forgot Password?</b>
                    </a>
                  </label>
                </div>
                <br />
                <center>
                  <div className="row">
                    <button
                      type="submit"
                      name="btn_login"
                      className="col s12 btn btn-large waves-effect indigo"
                    >
                      Login
                    </button>
                  </div>
                </center>
              </form>
            </div>
          </div>
          <a href="#!">Create account</a>
        </center>
        <div className="section" />
        <div className="section" />
      </main>
    </div>
  );
}