import "./card.css";

export function Card(props) {
  return (
    <div className="card">
      <div className="card-image waves-effect waves-block waves-light">
        <img className="activator"src={props.product_data.pic || "https://materializecss.com/images/office.jpg"} city="FSD"/>
      </div>
      <div className="card-content">
        <span className="card-title activator grey-text text-darken-4">{props.product_data.title}<i className="material-icons right">more_vert</i></span>
        <p>
          <a href="#">{props.product_data.price}</a>
        </p>
      </div>
      <div className="card-reveal">
        <span className="card-title grey-text text-darken-4">
          Card Title<i className="material-icons right">close</i>
        </span>
        <p>
          Here is some more information about this product that is only revealed
          once clicked on.
        </p>
      </div>
    </div>
  );
}
