import axios from "axios";
import { useEffect } from "react";
import { useForm } from "react-hook-form"
import { useDispatch, useSelector } from "react-redux";


export function CreateProducts(){
  let {register,handleSubmit} = useForm()
  let dispatch = useDispatch()

  let user = useSelector(function(store){
    return store.userSection.currentUser
  })


 async function userData(meraData){
    meraData.userId = user.id
    // meraData.picture = URL.createObjectURL(meraData.picture[0])
    // dispatch({
    //     type:'PRODUCT_ADD',
    //     payload:meraData
    // })

  let meraForm = new FormData();
  meraForm.append('id',meraData.userId)
  meraForm.append('title',meraData.title)
  meraForm.append('price',meraData.price)
  meraForm.append('pic',meraData.picture[0])
  let resp = await axios.post('/create_product',meraForm)

  }

   return <div>
    <form onSubmit={handleSubmit(userData)}>
        <input type='text' {...register('title')} />
        <input type='text' {...register('price')} />
        <input type='file' {...register('picture')} />
        <button>Submitt</button>
    </form>
   </div>
}