import { useDispatch, useSelector } from "react-redux"
import { Link } from "react-router-dom"
import './header.css'
export function Header(){
  let dispatch = useDispatch()
   let user = useSelector(function(store){
    return store.userSection.currentUser;
   })
    return <header>
        <nav>
        <div className="nav-wrapper">
          <Link to='/'> Logo</Link>
            {user.id!= undefined   && <span className="user-name">Welcome, <img className="pic-thumb" src={user.pic} /> <b>{user.name}</b></span>}
          <ul id="nav-mobile" className="right hide-on-med-and-down">
            { user.id == undefined &&
              <>
              <li><Link to='/singup' >Singup</Link></li>
              <li><Link to='/login' >Login</Link></li>
              </>
            }

            { user.id!= undefined &&
              <>
             <li><Link to='/users' >User</Link></li>
             <li><Link to='/CreateProducts' >Add-Products</Link></li>
             <li><Link to='/dashboard' >Dashboard</Link></li>
             <li><Link to='/login' onClick={function(){
              dispatch({
                type:'LOG_OUT'
              })
             }} >Log-Out</Link></li>
              </>
            }
            {/* <li><Link to='/food' >Food</Link></li> */}
            {/* <li><Link to='/calculator' >Calculator</Link></li> */}
            {/* <li><Link to='/CreateProduct' >Add-User</Link></li> */}
          </ul>
        </div>
      </nav>
    </header>
}

